//
// Color
//

// basic
$color-primary-dark                   : #222222;
$color-primary-light                  : #FCFCFC;
$color-primary                        : #E2E2E2;


$color-danger                         : rgb(213, 049, 049); // #d53131: reddish
$color-warn                           : rgb(239, 132, 060); // #ef843c: dusty-orange

// gray
$gray-black                           : rgb(000, 000, 000); // #000000
$gray-darkest                         : rgb(097, 103, 111); // #61676f: slate-grey
$gray-darker                          : rgb(129, 136, 145); // #818891: steel
$gray-dark                            : rgb(170, 178, 188); // #aab2bc: light-grey-blue
$gray                                 : rgb(186, 196, 211); // #bac4d3: cloudy-blue
$gray-light                           : rgb(223, 225, 230); // #dfe1e6: light-gray
$gray-lighter                         : rgb(243, 245, 246); // #f3f5f6: ice-gray
$gray-lightest                        : rgb(241, 243, 249); // #f1f3f9: light-pale-grey
$gray-white                           : rgb(255, 255, 255); // #ffffff


//
// Transition
//

$transition-mode                      : cubic-bezier( .25, .8, .25, 1);


//
// Texting
//

$font-family                          : "Lato", "-apple-system", "PingFang-TC", "Segoe UI", "Microsoft JhengHei", "Roboto", "Oxygen", "Fira Sans", "Helvetica Neue", "Helvetica", "Arial", "sans-serif";

$font-size-xl                         : 20px;
$font-size-lg                         : 18px;
$font-size-md                         : 16px;
$font-size-sm                         : 14px;
$font-size-xs                         : 12px;


//
// Usage
//
$mobile-width: 375px;
$table-width: 750px;
$laptop-width: 992px;
$desktop-width: 1200px;

//
// RWD
//

@mixin above-mobile() {
  @media screen and (min-width: $mobile-width) {
    @content;
  }
}

@mixin above-table() {
  @media screen and (min-width: $table-width) {
    @content;
  }
}

@mixin above-laptop() {
  @media screen and (min-width: $laptop-width) {
    @content;
  }
}

@mixin above-desktop() {
  @media screen and (min-width: $desktop-width) {
    @content;
  }
}

$types: (
  default: (
    filled-color: $color-primary-dark,
    text-color: $color-primary-light
  )
);
$sizes: (
  xs: (
    padding: 12px 23px,
    height: 40px,
    width: 120px,
    font-size: $font-size-xs,
    radius: 50px
  )
);

.button {
  @each $type, $styles in $types {
    &:not(:disabled)[data-type='#{$type}'] {
      color: map-get($styles, text-color);
      background-color: map-get($styles, filled-color);
    }
  }

  @each $size, $styles in $sizes {
    &[data-size="#{$size}"] {
      padding: map-get($styles, padding);
      height: map-get($styles, height);
      width: map-get($styles, width);
      font-size: map-get($styles, font-size);
      border-radius: map-get($styles , radius);
    }
  }
}
